<template>
  <div>
    <b-navbar fixed="top" type="dark" id="topnavbarproperties">
      <!-- left -->
      <b-navbar-nav class="mr-auto">
        <b-nav-item href="#">
          <font-awesome-icon id="burgerbutton" icon="bars" style="margin-right: 15px" v-b-toggle.sidebar-1></font-awesome-icon>
          <router-link to="/"><font-awesome-icon icon="home"></font-awesome-icon></router-link>
        </b-nav-item>
      </b-navbar-nav>

      <!-- center -->
      <b-navbar-nav class="mx-auto order-0">
        <b-nav-item><router-link to="/"><img width="115px" src="../../public/image/louiselize.png" alt="louise lize"></router-link></b-nav-item>
      </b-navbar-nav>

      <!-- right -->
      <b-navbar-nav class="ml-auto">
        <b-nav-item-dropdown right>
          <template slot="button-content">
            <font-awesome-icon icon="language"/>
          </template>
          <b-dropdown-item @click="changeLibrary('english')"> English
            <font-awesome-icon
                v-show="language==='english'"
                icon="check"
                style="margin-left: 20px"
            ></font-awesome-icon>
          </b-dropdown-item>
          <b-dropdown-item @click="changeLibrary('french')"> Franais
            <font-awesome-icon
                v-show="language==='french'"
                icon="check"
                style="margin-left: 20px"
            ></font-awesome-icon>
          </b-dropdown-item>
          <b-dropdown-item @click="changeLibrary('spanish')" disabled> Espaol
            <font-awesome-icon
                v-show="language==='spanish'"
                icon="check"
                style="margin-left: 20px"
            ></font-awesome-icon>
          </b-dropdown-item>
          <b-dropdown-item @click="changeLibrary('german')" disabled> Deutsch
            <font-awesome-icon
                v-show="language==='german'"
                icon="check"
                style="margin-left: 20px"
            ></font-awesome-icon>
          </b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-navbar>
    <b-sidebar id="sidebar-1" class="sidebar" title="Sidebar" style="color:white" no-header shadow>

      <div>
        <left-menu></left-menu>
      </div>
    </b-sidebar>
  </div>
</template>

<script>
import {store} from "../main";
import LeftMenu from "./LeftMenu";

export default {
  name: "TopNavbar",
  components: {LeftMenu},
  data(){
    return {
      language:store.state.language,
      library:store.state.library
    }
  },
  methods:{
    changeLibrary(choosenLangage) {
      store.changeLibrary(choosenLangage)
      this.language=store.state.language
      this.library=store.state.library
    }
  },
}
</script>

<style scoped>
a{
  color: white;
}
#topnavbarproperties{
background: linear-gradient(  180deg, #12769E 0%, #1278BA 100%);
  z-index: 30;

}
div#sidebar-1 {
  top:0;
}
@media (min-width:1001px ) {
  #burgerbutton{
    display: none;
  }
  #sidebar-1{
    display: none;
  }
  .sidebar{
    display: none;
  }
}
#burgerbutton{

}
</style>