<template>
<div id="utc">
  <div id="rightitle">
    <b-breadcrumb>
      <b-breadcrumb-item><router-link to="/">{{ storeState.library.upperHome }}</router-link></b-breadcrumb-item>
      <b-breadcrumb-item><router-link to="/formation">{{ storeState.library.upperEducation }}</router-link></b-breadcrumb-item>
      <b-breadcrumb-item active>UTC</b-breadcrumb-item>
    </b-breadcrumb>
  </div>
  <div class="toplogo">
    <b-container>
      <b-row>
        <b-col cols="0" lg="3">
        </b-col>
        <b-col cols="12" lg="6">
          <a href="https://www.utc.fr/" target="_blank"><img id="utclogo" src="../../public/image/fullLogoUTC.png"></a>
        </b-col>
        <b-col cols="0" lg="3">
        </b-col>
      </b-row>
    </b-container>
    <br>
  </div>

  <div class="rightitem">
    <b-container>
      <b-row>
        <b-col class="utcdescription" cols="12" sm="12" lg="4">
          <h5><strong> {{ storeState.library.utcDescription.utcInFewWords.title }}</strong></h5>
          {{ storeState.library.utcDescription.utcInFewWords.text }}<br>
        </b-col>
        <b-col class="utcdescription" cols="12" sm="12" lg="4">
          <h5><strong>{{ storeState.library.utcDescription.utcObjectives.title }}</strong></h5>
          {{ storeState.library.utcDescription.utcObjectives.text }}
        </b-col>
        <b-col class="utcdescription" cols="12" sm="12" lg="4">
          <h5><strong>{{ storeState.library.utcDescription.utcStrongPoints.title }}</strong></h5>
          <ul style="text-align: left">
            <li>{{ storeState.library.utcDescription.utcStrongPoints.text.point1 }}</li>
            <li>{{ storeState.library.utcDescription.utcStrongPoints.text.point2 }}</li>
            <li>{{ storeState.library.utcDescription.utcStrongPoints.text.point3 }}</li>
            <li>{{ storeState.library.utcDescription.utcStrongPoints.text.point4 }}</li>
          </ul>
        </b-col>
      </b-row>

    </b-container>
  </div>
  <div class="rightitem">
      <b-container>
        <b-row>
          <b-col>
            <strong>2018-2020: {{ storeState.library.commonCore }} </strong>
          </b-col>
        </b-row>
        <b-row class="row align-items-center">
          <b-col cols="12" md="6">
            <ul>
              <li>3 {{ storeState.library.semestersInUTC }}</li>
              <li>1 {{ storeState.library.semesterErasmus }}</li>
              <li>1 {{ storeState.library.companyImmersionInternship}} </li>
            </ul>
          </b-col>
          <b-col cols="12" md="6" style="font-size: large">
            <div style="text-align: center; font-weight: bold;">
              <span style="color: #12769E">GPA : 4,02/5</span>
            </div>
          </b-col>
        </b-row>
        <b-row>

        </b-row>
        <b-row>
          <b-table sticky-header="325px"	size="sm" style="text-align: center" :class="library" responsive caption-top :fields="fields" :items="commonCoreItems"  striped>
            <template #table-caption>
              <span>
                {{ storeState.library.relevantCourses }}
                <font-awesome-icon class="search" v-if="displayDescription===false" @click="setDisplayDescription(true)" icon="search-plus"></font-awesome-icon>
                <font-awesome-icon class="search" v-else-if="displayDescription===true" @click="setDisplayDescription(false)" icon="search-minus"></font-awesome-icon>
              </span>
            </template>
            <template v-slot:cell(courseDescription)="description">
              <div v-show="displayDescription" style="text-align: justify">{{ description.item.courseDescription }}</div>
            </template>
            <template v-slot:head(courseDescription)="description">
              <div v-show="displayDescription" style="text-align: justify">{{ description.label }}</div>
            </template>
            <template v-slot:cell(courseProject)="description">
              <div v-if="description.item.courseProject===false"><font-awesome-icon icon="times"/></div>
              <div v-else-if="description.item.courseProject===true"><font-awesome-icon @click="setSlide(description.item.slide)" class="eye-icon" icon="eye"/></div>
            </template>
          </b-table>
          <em>* {{ storeState.library.honors}}</em>
        </b-row>
      </b-container>
    </div>
  <div class="rightitem">
      <b-container>
        <b-row>
          <b-col>
            <strong>2020-2023: {{ storeState.library.majorStudies }}</strong>
          </b-col>
        </b-row>
        <b-row class="row align-items-center">
          <b-col cols="12" md="6">
            <ul>
              <li>3 {{ storeState.library.semestersInUTC }}</li>
              <li>6 {{ storeState.library.assistantEngineerInternship }}</li>
              <li> 1 {{ storeState.library.doubleDiploma }}</li>
              <li>6 {{ storeState.library.endOfStudyInternship }}</li>
            </ul>
          </b-col>
          <b-col cols="12" md="6" style="font-size: large">
            <div style="text-align: center; font-weight: bold;">
              <span style="color: #12769E">GPA : 5/5</span>
            </div>
          </b-col>
        </b-row>
        <b-row>
          <b-table sticky-header="325px"	size="sm" style="text-align: center;" :class="library" responsive caption-top :fields="fields" :items="majorStudiesItems"  striped>
            <template #table-caption>
              <span>
                {{ storeState.library.relevantCourses }}
                <font-awesome-icon class="search" v-if="displayDescription===false" @click="setDisplayDescription(true)" icon="search-plus"/>
                <font-awesome-icon class="search" v-else-if="displayDescription===true" @click="setDisplayDescription(false)" icon="search-minus"/>
              </span>
            </template>
            <template v-slot:cell(courseDescription)="description">
              <div v-show="displayDescription" style="text-align: justify">{{ description.item.courseDescription }}</div>
            </template>
            <template v-slot:head(courseDescription)="description">
              <div v-show="displayDescription" style="text-align: justify">{{ description.label }}</div>
            </template>
            <template v-slot:cell(courseProject)="description">
              <div v-if="description.item.courseProject===false"><font-awesome-icon icon="times"/></div>
              <div v-else-if="description.item.courseProject===true"><font-awesome-icon @click="setSlide(description.item.slide)" class="eye-icon" icon="eye"/></div>
            </template>
          </b-table>
          <em>* {{ storeState.library.honors}}</em>
        </b-row>
      </b-container>
  </div>
  <br><br>
</div>
</template>

<script>
import {store} from "../main";
import router from "../router";

export default {
  name: "UTC",
  data(){
    return{
      storeState: store.state,
      commonCoreItems:[],
      majorStudiesItems:[],
      displayDescription:false,
      fields: []
    }
  },
  methods:{
    setDisplayDescription:function(boolean){
      this.displayDescription=boolean
    },

    setSlide:function(slide){
      store.state.slide=slide
      router.push('/informatique')
    },
    initialisationTable:function(){
      this.fields = [
        {
          key: 'courseShortName',
          label: store.state.library.courseShortName,
          thClass: 'align-middle',
          tdClass: 'align-middle',

        },
        {
          key: 'courseLongName',
          label: store.state.library.courseLongName,
          thClass: 'align-middle',
          tdClass: 'align-middle',

        },
        {
          key: 'courseDescription',
          label: store.state.library.courseDescription,
          thClass: 'align-middle',
          tdClass: 'align-middle',

        },
        {
          key: 'courseType',
          label: store.state.library.courseType,
          thClass: 'align-middle',
          tdClass: 'align-middle',

        },
        {
          key: 'courseMark',
          label: store.state.library.courseMark,
          thClass: 'align-middle',
          tdClass: 'align-middle',

        },
        {
          key: 'courseProject',
          label: store.state.library.courseProject,
          thClass: 'align-middle',
          tdClass: 'align-middle',

        },

      ]
      this.commonCoreItems = [
        store.state.library.MT91,store.state.library.MT22,store.state.library.MT23, store.state.library.NF16,store.state.library.NF93,
        store.state.library.TN04,store.state.library.NF01,store.state.library.NF92,
        store.state.library.LA13,store.state.library.LA23,store.state.library.PH01
      ]

      this.majorStudiesItems = [
          store.state.library.SY02, store.state.library.SY08,store.state.library.IA02,
          store.state.library.LO21,store.state.library.NF18,
          store.state.library.LA01,store.state.library.SI28,
      ]

    }

  },
  beforeMount() {
    this.initialisationTable()
  },
  computed:{
    library:function (){
      this.initialisationTable()
      return store.state.language
    }
  }

}
</script>

<style scoped>

.search{
  font-size: 25px;
  float: right;
  margin-right: 10px;

}
@media (max-width: 1000px) {
  .utcdescription{
    margin-bottom: 15px
  }
}
@media (min-width: 1001px) {
  .utcdescription{
    text-align: center;
  }
}
#utclogo{
  width: 175px;
}
.toplogo{
  width: 80%;
  font-size: 95%;
  margin-left: auto;
  margin-right: auto;
}

.eye-icon{
  color: #0A0F57;
}

.eye-icon:hover{
  font-size: larger;
  color: #0A0F57;
}
</style>