import '@babel/polyfill'
import 'mutationobserver-shim'
import Vue from 'vue'
import VueMeta from 'vue-meta'
import './plugins/fontawesome'
import './plugins/bootstrap-vue'
import App from './App.vue'
import router from "./router";
import Home from "./components/Home";
import '@fortawesome/fontawesome-free/css/all.css'
import '@fortawesome/fontawesome-free/js/all.js'
import Formation from "./components/Formation";
import Experience from "./components/Experience";
import Informatique from "./components/Informatique";
import Langue from "./components/Langue";
import CentreInteret from "./components/CentreInteret";
import Condorcet from "./components/Condorcet";
import UTC from "./components/UTC";
import TUKE from "./components/TUKE";
import {frenchLibrary} from "./library/frenchLibrary";
import {englishLibrary} from "./library/EnglishLibrary";
import {spanishLibrary} from "./library/spanishLibrary";
import {germanLibrary} from "./library/germanLibrary";
import CountryFlag from 'vue-country-flag'
import Olvid from "./components/Olvid";
import SaintGobain from "./components/SaintGobain";
import StudentJobs from "./components/StudentJobs";
Vue.component('country-flag', CountryFlag)

Vue.config.productionTip = false

Vue.use(VueMeta)

export const store = {
  state: {
    language:'french',
    library:frenchLibrary,
    slide:0,
    navbarItemSelected:'',
  },

  changeLibrary(choosenLangage){
    store.state.language=choosenLangage
    switch (store.state.language){
      case 'french' :
        store.state.library=frenchLibrary
        break;
      case 'english' :
        store.state.library=englishLibrary
        break;
      case 'spanish' :
        store.state.library=spanishLibrary
        break;
      case 'german' :
        store.state.library=germanLibrary
        break;
      default:
        store.state.library=frenchLibrary

    }
  }

}
router.addRoutes([
    {
  path: '/',
  name:'accueil',
  component:  Home
  },
  {
    path: '/formation',
    name:'formation',
    component:  Formation
  },
  {
    path: '/experience',
    name:'experience',
    component:  Experience
  },
  {
    path: '/informatique',
    name:'informatique',
    component:  Informatique,
  },
  {
    path: '/langue',
    name:'langue',
    component:  Langue,
  },
  {
    path: '/centre-interet',
    name:'centreInteret',
    component:  CentreInteret
  },
  {
    path: '/formation/UTC',
    name:'UTC',
    component:  UTC
  },
  {
    path: '/formation/TUKE',
    name:'TUKE',
    component:  TUKE
  },
  {
    path: '/formation/Condorcet',
    name:'Condorcet',
    component:  Condorcet
  },
  {
    path: '/experience/olvid',
    name:'olvid',
    component:  Olvid
  },
  {
    path: '/experience/saint-gobain',
    name:'saint-gobain',
    component:  SaintGobain,
  },
  {
    path: '/experience/studentjobs',
    name:'studentjobs',
    component:  StudentJobs
  },
]),


new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
