<template>
<div id="informatique">
  <div id="rightitle">
    <b-breadcrumb>
      <b-breadcrumb-item><router-link to="/">{{ storeState.library.upperHome }}</router-link></b-breadcrumb-item>
      <b-breadcrumb-item active>{{ storeState.library.upperComputerScience }}</b-breadcrumb-item>
    </b-breadcrumb>
  </div>
  <div class="rightitem">
    <div>
      <b-container>
        <b-row>
          <b-col style="text-align: center">
            <h6><strong style="color: #0A0F57;">{{ storeState.library.computerScienceProjects.title }}</strong></h6>
          </b-col>
        </b-row>
        <b-row class="row align-items-center">
          <b-col style="margin-right: auto; margin-left: auto; width: 100%" cols="12" sm="12" md="8" lg="9" xl="7">
            <b-carousel
                id="carousel"
                v-model="slide"
                :interval="0"
                controls
                indicators
                background="#ababab"
                img-width="1024px"
                img-height="480px"
                style="border-radius:5px;border:solid 1px lightgrey;text-shadow: 1px 1px 2px #333; font-size: 75%"
                @sliding-start="onSlideStart"
                @sliding-end="onSlideEnd"
            >
              <img
                  v-show="slidesAttributes.isUtcProject"
                  id="carousel-image"
                  src="../../public/image/logoUTC.png"
                  v-b-tooltip.hover.v-light="{
                          title:storeState.library.computerScienceProjects.tooltipUTCProject
                  }"
              />

              <span id="projectNumber">{{ slide+1}} / {{storeState.library.computerScienceProjects.projects.length }}</span>
              <!--<span id="caption">{{ slidesAttributes.title }}</span>-->

              <span :key="index" v-for="(project,index) in storeState.library.computerScienceProjects.projects">
                <b-carousel-slide :img-src="require('../../public/image/' + project.imageSource)">
              </b-carousel-slide>
              </span>

            </b-carousel>

          </b-col>
          </b-row>
          <b-row class="row align-items-center">
            <b-col cols="12">
              <p class="mt-4" style="text-align: left">
                <b-container>
                  <b-row>
                    <b-col>
                      <h5>{{ storeState.library.computerScienceProjects.projects[slide].title }}</h5>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="1" class="p-icon">
                      <font-awesome-icon icon="clock"/>
                    </b-col>
                    <b-col cols="10" sm="11">
                      {{ storeState.library.computerScienceProjects.projects[slide].date }}
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="1" class="p-icon">
                      <font-awesome-icon icon="users"/>
                    </b-col>
                    <b-col cols="10" sm="11">
                      {{ storeState.library.computerScienceProjects.projects[slide].participantsNumber }}
                    </b-col>
                  </b-row>
                  <b-row v-show="storeState.library.computerScienceProjects.projects.isCode">
                    <b-col cols="1" class="p-icon">
                      <font-awesome-icon icon="file-code"/>
                    </b-col>
                    <b-col cols="10" sm="11">
                      {{ storeState.library.computerScienceProjects.projects[slide].codeLink }}
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="1" class="p-icon">
                      <font-awesome-icon icon="code"/>
                    </b-col>
                    <b-col cols="10" sm="11">
                      {{ storeState.library.computerScienceProjects.projects[slide].technologies }}
                    </b-col>
                  </b-row>
                  <b-row v-show="storeState.library.computerScienceProjects.projects[slide].isLink">
                    <b-col cols="1" class="p-icon">
                      <font-awesome-icon icon="link"/>
                    </b-col>
                    <b-col cols="10" sm="11">
                      <a style="color: #0A0F57; font-weight: bold" target="_blank" :href="storeState.library.computerScienceProjects.projects[slide].link">{{ storeState.library.link }}</a>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="1" class="p-icon">
                      <font-awesome-icon icon="tasks"/>
                    </b-col>
                    <b-col cols="10" sm="11">
                      {{ storeState.library.computerScienceProjects.projects[slide].tasks }}
                    </b-col>
                  </b-row>
                </b-container>
              </p>
            </b-col>
          </b-row>
      </b-container>
    </div>
  </div>
  <br><br><br>
</div>
</template>

<script>
import {store} from "../main";

export default {
  name: "Informatique",
  data(){
    return{
      storeState: store.state,
      slide: store.state.slide,
      slidesAttributes:store.state.library.computerScienceProjects.projects[0],
      sliding: null
    }
  },
  methods: {
    onSlideStart() {
      this.sliding = true
    },
    onSlideEnd() {
      this.sliding = false
    }
  },
  watch:{
    slide:function (){
      this.slidesAttributes=store.state.library.computerScienceProjects.projects[this.slide]
    }
  }
}
</script>

<style scoped>

#carousel{
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

#caption{
  position: absolute;
  color : white;
  text-align: center;
  font-size: 2vw;
  z-index: 800;
  right: 0;
  left:0;
  width: 75%;
  top:60%;
  margin: auto;
}

#projectNumber{
  position: absolute;
  text-align: right;
  color : white;
  z-index: 800;
  width: 15%;
  right: 15px;
  bottom: 10px;
}

#carousel-image{
  position: absolute;
  z-index: 800;
  width: 10%;
  right: 8px;
  top: 10px;
}

@media (max-width: 1000px) {
  .p-icon{
    text-align: center;
  }
}

@media (min-width: 1001px) {
  .rightitem{
    height: calc(100vh - 150px);
  }
}


</style>